import { Component, ViewChild, OnInit, AfterViewInit, ElementRef } from '@angular/core';
import * as FusionCharts from 'fusioncharts';
import { MedicalSearchPageComponent } from '../medical-search-page/medical-search-page.component';
import { SearchPhraseSharingServiceService } from '../search-phrase-sharing-service.service';
import { TimelineLite, TimelineMax } from 'gsap';
import * as $ from 'jquery';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-medical-search-results',
  templateUrl: './medical-search-results.component.html',
  styleUrls: ['./medical-search-results.component.css']
})

export class MedicalSearchResultsComponent implements OnInit, AfterViewInit {

  @ViewChild(MedicalSearchPageComponent) medicalSearchPage!: MedicalSearchPageComponent;

  //@ViewChild('severityLevel') level!: ElementRef;

  @ViewChild('sliderRange') slider!: ElementRef;

  searchPhrase = "de quervain";
  ageValue = 15;
  yesNos: string[] = ["Yes", "No"];
  genders: string[] = ["Male", "Female"];
  noOfDays: string[] = ["0-3 Days", "4+ Days"];
  painArray: string[] = ["YAS", "Yeah", "Mmkay", "Ouch", "Owwwww", "AHHHHHHH"];
  pain: string[] = [];
  needle: any;
  prevFace: any;
  disabled = true;
  gender: string="Male";
  public imageUrl = 'image.svg';
  image = 'https://www.completedietsolutions.org/Member/completedietsolutions/Images/woman-smiling-png-e1422243041656.png';

  constructor(private searchPhraseSharingServiceService: SearchPhraseSharingServiceService, carousel: NgbCarouselConfig) { }
  
  
  ngAfterViewInit(): void {

    // this.needle = $("#needle");
    // this.prevFace = ".faces #Ouch";

    $('.pain-scale__level').hover(function(){
      $(this).addClass( "pain-scale__level--active").prevAll().addClass( "pain-scale__level--active");
    }, function(){
      $(this).removeClass( "pain-scale__level--active").prevAll().removeClass( "pain-scale__level--active");
    });
    
    $('.pain-scale__level').click(function(){
      $(this).siblings().removeClass("pain-scale__level--selected pain-scale__level--active-emoji");
      $(this).addClass( "pain-scale__level--selected pain-scale__level--active-emoji pain-scale__level--blink").one("webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend", function(){
        $(this).removeClass( "pain-scale__level--blink" );
      });
      $(this).prevAll().addClass( "pain-scale__level--selected pain-scale__level--blink").one("webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend", function(){
        $(this).removeClass( "pain-scale__level--blink" );
      });
    });
  }

  ngOnInit(): void {
    console.log('trying to access');
    this.searchPhrase = this.searchPhraseSharingServiceService.searchPhrase;

    this.painArray = ["YAS", "Yeah", "Mmkay", "Ouch", "Owwwww", "AHHHHHHH"];

    for (var i = 0; i < this.painArray.length; i++) {
      this.pain[i] = this.painArray[i];
    }
  }
  findPainLevel(value: number) {
    var painLevel = Math.floor(value / (Math.round(100 / 6)));
    return painLevel;
  }

  checkGender(gender: string){
    console.log("gender check", this.gender)
    if(gender=="Female"){
      this.disabled=false;
    } else{
      this.disabled=true;
    }
  }

  moveGauge(input: number) {
    var value = Number(input);
    var maxScale = 943;
    var minScale = 471;
    var maxDegree = 85;
    var scaledValue = ((maxScale - minScale) / 100) * value + minScale;
    var midScale = (maxScale - minScale) / 2 + minScale; // mid is actually rotate 0

    if (scaledValue <= midScale) {
      var rotate = 1;
    }
    else {
      var rotate = -1;
    }

    var scalePerDegree = (midScale - minScale) / maxDegree;
    var rotateValue = (midScale - scaledValue) / scalePerDegree;
    var rotation = rotate * rotateValue;
    var tl = new TimelineLite();
    tl.to(this.needle,  1, {rotation: rotateValue });
  }

  morphFace(painLevel: any){
    var mouthExpression;
    console.log("painlvl ",painLevel)
    mouthExpression = "#mouth_" + painLevel;
    console.log("mouthexprn ",mouthExpression)
    var facialExpression = ".faces #" + this.pain[painLevel];
    console.log("facial ",facialExpression)
    var tl = new TimelineMax();
    tl.to("#mouth_3",  1,{ morphSVG:mouthExpression});
    $(this.prevFace).css("visibility", "hidden");
    $(facialExpression).css("visibility", "visible");
    this.prevFace = facialExpression;
  }

  visibilityChange(value: any) {
    
    var currentLevel = Number(value.target.value);
    var painLevel = this.findPainLevel(currentLevel);
    this.moveGauge(currentLevel);
    this.morphFace(painLevel);
  }
  formatLabel(value: number) {
    return value;
  }

  onMouseEnter(event: any): void {
    event.target.click();
  }
}